body {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
}

div {
  display: flex;
}

.body h1 {
  flex: 1;
}

.coutdown {
  flex: 1;
  flex-direction: row;
}

.day, .hour, .minute, .second {
  flex: 1;
  flex-direction: column;
}

.upper {
  flex: 1;
  width: 100px;
  height: 100px;
  background-color: hsl(236, 21%, 26%);
}

.lower {
  flex: 1;
  width: 100px;
  height: 100px;
  background-color: hsl(235, 16%, 14%);
}
